import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Demo from '@/components/Demo'
import Test from '@/components/Test'
import Cart from '@/components/Cart'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/demo',
      name: 'Demo',
      component: Demo
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart
    }
  ]
})


